header {
  position: relative;
  width: 100%;
  min-height: auto;
  overflow-y: hidden;
  background: #439774;
  background: -webkit-linear-gradient(to left, #439774, #243bdc);
  background: linear-gradient(to left, #439774, #243bdc);
  color: #fff;
}
header .header-content {
  text-align: center;
  padding: 150px 0 50px;
  position: relative;
}
header .header-content .header-content-inner {
  position: relative;
  max-width: 500px;
  margin: 0 auto;
}
